.image-container:hover{
  position: relative;
  transition: transform 0.2s;
}

.image-container:hover .overlay{
  opacity: 1;
}

.overlay{
  position: absolute;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  transition: 0.5s ease;
  opacity: 0;
  bottom: 0;
  font-size: 20px;
  padding: 20px;
  text-align: center;
}


